.image-block{
  border: 10px solid #12121250;
  border-radius: 40px;
}

.image{
  width: 100%;
  height: 100%;
  border-radius: 20px;
  border: 8px solid #c5c5c5;
  padding: 0px;
  transition: 0.5s;
}

.image:hover{
  transform: scale(1.1);
  transition: 0.5s;
  overflow: hidden;
}